/* Responsive Stage Indicator*/


.responsive-stage-indicator {
	display: none;
	position: fixed;
	bottom: 0;
	right: 0;
	background-color: #aaa;
	padding: 0.2em 0.5em;
	color: #fff;
	opacity: 0.5;
}
.responsive-stage-indicator.global {
	display: block;
}	

.clearfix:after {
   content: " "; /* Older browser do not support empty content */
   visibility: hidden;
   display: block;
   height: 0;
   clear: both;
}

body {	
	font-family: 'Open Sans', sans-serif;
	color: #1e3645;
	font-size: 18px;
	font-weight: normal;
	background-color: #f8f8f8; /*#f0efef;*/
}

h1 {
	font-weight: normal;
	font-style: normal;	
	float: left;
	width: 100%;
	font-size: 22px;
}

h1.page-title {
	margin: 38px auto 38px auto;		
	float: none;
	text-align: center;
	font-size: 38px;
	color: #1e3645;
}

.has-submenu h1.page-title {
	color: #4195d1;
	float: left;
	text-transform: none;
	text-align: left;
	font-size: 26px;
}

h2 {	
	font-weight: normal;
  font-size: 1.2em;
}

h3 {
	font-weight: normal;
	text-decoration: none;
	font-size: 18px;
}

a {	
	text-decoration: none;
}

h3 a {
	text-decoration: none;
}

img {
	max-width: 100%;
	height: auto;
}

hr {
	margin: 10px 0;
}

article {
	overflow: hidden;
}

body {
	background-image: url('../images/low_contrast_linen.png');
	background-color: #333;
}

/* Layout */

.full-wrap {
	width: 100%;		
}
.full-inner {
	width: 96%;
	max-width: 1000px;
	margin: 0 auto;
}

header {
  border-top: 2px solid #6AC3DB;
  z-index: 1;
  width: 100%;
  top: 0;
  text-align: center;
  color: #fff;
  margin-top: 100vh;
  width: 100%;
  display: block;
}

.main-menu {
	text-align: center;
	width: 100%;
  display: block;
  overflow: hidden;
}

.main-menu.sticky {
	position: fixed;
	top: 0;
	background-image: url('../images/low_contrast_linen.png');
	left: 0;
	z-index: 10;
	border-bottom: 2px solid #6AC3DB;
	
}

.main-menu ul {
	margin-left: 0;
	text-align: center;
	
}

.main-menu.sticky ul {
	margin-left: auto;
	margin-right: auto;
	max-width: 96%;
}

.main-menu li {
	display: inline-block;
	text-transform: uppercase;
	font-size: 20px;
}

.main-menu li a {
	color: #aaa;
	padding: 0 13px;
}

.main-menu li.active a {
	color: #fff;
}

.main-menu li a:hover {
	border-top: 2px solid #6AC3DB;
	
}

.main-menu li.services a:hover{
	border-top: 2px solid #ff0000;
}

.main-menu li.portfolio a:hover{
	border-top: 2px solid #ffb300;
}

.main-menu li.contact a:hover{
	border-top: 2px solid #fff500;
}

.main-menu li.portfolio a:hover{
	border-top: 2px solid #00ee00;
}

.main-menu li.menu-441 a:hover{
	border-top: 2px solid #032fe4;
}

.main-menu li.menu-666 a:hover{
	border-top: 2px solid #7e02e3;
}


.section-contents {
	text-align: center;
}

.portfolio-summary-item {
	margin-right: 0;
  max-width: 100%; 
  margin-bottom: 30px;
  cursor: pointer;
  display: inline-block;
}

.portfolio-summary-item:nth-child(3n+1) {
	clear: both;
}


.portfolio-summary-item {
	position: relative;
}

.portfolio-summary-item img {
	border: 1px solid #ccc;
	display: block;
}

.portfolio-summary-item .curtain {
	background-image: url("../images/A-gradient.png");	
	position: absolute;  
  background-repeat: repeat-x;
  background-position: 0 100%;
  width: calc(100% + 2px);
  height: 100%;
  background-color: rgba(0,0,0,0.6);
}

.portfolio-summary-item .textual {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: calc(100% - 40px);
  margin:  20px;
  color: #fff;
  font-weight: 600; 
  text-align: center;
}  

.portfolio-summary-item .textual h3 {
	font-weight: 200; 
	margin-top: 0;
	text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.33);
	font-size: 28px;
	line-height: 1.2em;
	margin-bottom: 0;
}

.portfolio-summary-item .textual a {
	color: #fff;
}

.path-frontpage h1.page-title {
	display: none;
}

h2.centred-subheader {	
  text-transform: uppercase;
  font-size: 36px;
  text-align: center;
  font-weight: 400;
  color: #555;
  margin-top: 80px;
}

.contact-message-contact-form label {
	width: 100%;
  display: block;
  margin-bottom: 0.5em;
}

.contact-message-contact-form input.form-text,
.contact-message-contact-form input.form-email,
.contact-message-contact-form textarea {
  border: 1px solid #ccc;
  display: inline-block;
  width: calc(100%);
}

.contact-message-contact-form textarea {
	width: calc(100%);
}

.contact-message-contact-form #edit-preview {
	display: none;
}

.section-contents.services {
	text-align: center;
}

.section-contents.services ul {
	text-align: left;
  display: inline-block;
}  

h2#services {
	margin-top: 40px;
}



