@media all and (min-width: 640px) and (min-device-width: 640px) {

	.responsive-stage-indicator.global {
		display: none;
	}
	.responsive-stage-indicator.narrow {
		display: block;
	}
	
	.portfolio-summary-item {
		margin-right: 30px;
		width: calc(33% - 18px);
		float: left;			
	}

	.portfolio-summary-item:nth-child(3n+1) {
		clear: both;
	}
	.portfolio-summary-item:nth-child(3n+0) {
		margin-right: 0;
	}

	
	
	.main-menu li {	
		font-size: 24px;
	}
	

}